.layout {
    display: flex;
  }
  
  .mainContent {
    /* Default state: when sidebar is expanded */
    margin-left: 250px;
    width: calc(100% - 250px);
    transition: all 0.3s; /* Smooth transition for margin adjustment */
  }
  
  .sidebarCollapsed .mainContent {
    /* When sidebar is collapsed, no margin on the left */
    margin-left: 0;
    width: 100%; /* Takes the full width when sidebar is collapsed */
  }
  
  .content {
    padding: 20px;
  }
  