/* Sidebar Styles */
.sidebar {
    background-color: #1f1f2f; /* Dark background color */
    color: white; /* White text color */
    width: 250px; /* Default sidebar width */
    height: 100vh; /* Full height */
    position: fixed; /* Fixed position on the left side */
    top: 0;
    left: 0;
    transition: all 0.3s; /* Smooth transition for width change */
    display: flex;
    flex-direction: column; /* Stack items vertically */
  }
  
  /* Collapsed state */
  .collapsed {
    width: 80px; /* Reduced width when collapsed */
  }
  
  /* Logo Styles */
  .logo {
    padding: 20px;
    text-align: center; /* Center the logo */
  }
  
  .logo img {
    width: 40px; /* Logo size */
  }
  
  /* Navigation List Styles */
  .navList {
    list-style: none; /* Remove default list styles */
    padding: 0;
    margin: 0;
  }
  
  /* Navigation Item Styles */
  .navItem {
    margin: 0;
  }
  
  /* Link Styles */
  .navLink {
    display: flex;
    align-items: center; /* Align icons and text */
    padding: 15px; /* Padding for each nav link */
    text-decoration: none; /* Remove underline from links */
    color: white; /* White text color */
    transition: all 0.3s; /* Smooth transition for hover effect */
  }
  
  /* Hover effect for links */
  .navLink:hover {
    background-color: #444; /* Darker background on hover */
  }
  
  /* Text visibility control */
  .navLink span {
    margin-left: 10px; /* Space between icon and text */
  }
  
  /* Hide text in collapsed state */
  .collapsed .navLink span {
    display: none;
  }
  
  /* Sidebar toggle button */
  .toggleButton {
    background-color: transparent; /* No background for the button */
    border: none; /* Remove border */
    color: white; /* White color for the button */
    font-size: 1.2rem; /* Font size */
    margin: 10px; /* Margin for spacing */
    cursor: pointer; /* Pointer cursor on hover */
    align-self: flex-end; /* Align toggle button to the end (right) */
  }
  